import React, {Component} from 'react'
import UserPhotos from '../component/UserPhotos'
import UserInformation from '../component/UserInformation'
import Loader from '../component/Loader'
import {Link, IndexLink} from 'react-router'
import PieCharts from '../component/PieCharts'
import BarCharts from '../component/BarCharts'

class MainAccueil extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            error: null,
            isLoaded: false,
            item: []
        }
    }
    componentDidMount() {
        fetch("https://jsonplaceholder.typicode.com/users/10")
            .then(res => res.json())
            .then((result) => {
                this.setState({isLoaded: true, item: result});
            }, (error) => {
                this.setState({isLoaded: true, error});
            })
    }
    render() {
        const {error, isLoaded, item} = this.state;
        if (error) {
            return <div>Erreur: {error.message}</div>;
        } 
        else if (!isLoaded){
            return (
                <Loader />
            )
        }        
        else {
            return (
                <section>
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <h2>Mon Profil</h2>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-12 col-md-12 col-lg-4">
                                <h3>Gestion</h3>
                                <Link className="btn btn-primary btn-lg btn-block" to={'#'}>Gérer mes albums</Link>
                                <Link className="btn btn-secondary btn-lg btn-block" to={"/membres/10"}>Voir l'historique des échanges</Link>
                                <Link className="btn btn-secondary btn-lg btn-block" to={"/membres/10"}>Gérer les livraisons</Link>
                            </div>
                            <div className="col-sm-12 col-md-6 col-lg-4">
                                <UserInformation 
                                key={item.id} 
                                name={item.name} 
                                email={item.email} 
                                phone={item.phone} 
                                street={item.address.street} 
                                suite={item.address.suite} 
                                city={item.address.city} 
                                zipcode={item.address.zipcode}
                                />
                                <Link className="primary" to='#'>Modifier</Link>
                            </div>
                            <div className="col-md-6 col-lg-4">
                                <UserPhotos key={item.id} userId={item.id}/>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12 col-lg-8">
                                <h3>Échanges complétées par mois</h3>
                                <BarCharts />
                            </div>
                            <div className="col-md-12 col-lg-4">
                                <h3>Taux de succès des échanges</h3>
                                <PieCharts />
                            </div>
                        </div>
                    </div>
                </section>
            )
        }
    }

}
export default MainAccueil