import React, {Component} from 'react'
import Chart from 'chart.js'

class PieCharts extends React.Component {
    componentDidMount() {
        var ctx = this.refs.ChartBar.getContext('2d');
        var chart = new Chart(ctx, {
            type: 'bar',
            data: {
                labels: ['Janvier', 'Février', 'Mars','Avril','Mai','Juin','Juillet','Août','Septembre','Octobre','Novembre','Décembre'],
                datasets: [{
                    data: [12, 19, 30, 15, 25, 14, 48, 68, 58, 27, 33, 40],
                    backgroundColor: [
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)'
                    ],
                    borderColor: [
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)',
                        'rgba(55, 90, 127, 0.8)',
                        'rgba(68, 68, 68, 0.8)'
                    ],
                    borderWidth: 1
                }]
            },
            options: {
                legend: {
                    display: false,
                }
            }
        })
    }
    render() {
        return (
            <canvas ref="ChartBar"></canvas>
        )
    }
}

export default PieCharts